import { Button, Color, Component, Node, Sprite, SpriteFrame, UITransform, _decorator } from "cc";
import { AS } from "db://as_framework/scripts//ASComponent";
import { UIDrag } from "db://as_framework/scripts/UIDrag";
const { ccclass, property, requireComponent } = _decorator;

@ccclass("Swap")
export class MCQ extends AS(Component) {
  @property({ type: SpriteFrame }) private parentImage: SpriteFrame[] = [];
  @property({ type: SpriteFrame }) private dragImage: SpriteFrame[] = [];
  @property({ type: Node }) private DragContainer: Node;
  @property({ type: Node }) private ParentImageContainer: Node;
  @property({ type: Node }) private check: Node;
  @property({ type: Number }) private Answer: number[] = [];

  awake() {
    this._updateComponent();
    this.check.addComponent(Button);
    this.check.on(Button.EventType.CLICK, this._check,this);
  }

  _updateComponent() {
    for (let i = 0; i < this.parentImage.length; i++) {
      this.DragContainer.children[i].addComponent(Sprite).spriteFrame = this.dragImage[i];
      this.DragContainer.children[i].addComponent(UIDrag);
      this.ParentImageContainer.children[i].addComponent(Sprite).spriteFrame = this.parentImage[i];
    }
  }
  _check() {
    const _dragables = this.DragContainer.getComponentsInChildren(UIDrag);
    const _position= [];

    for(let j=0;j<this.DragContainer.children.length;j++)
    {
    _position.push(this.DragContainer.children[0].getPosition().y);
    }

    _dragables.forEach((dragable, i) => {
      console.log(this.Answer[i].toString(),"comp",this.DragContainer.children[i].name)
      if (this.Answer[i].toString() == this.DragContainer.children[i].name) {
        console.log("right ans"+i)
        dragable.getComponent(Sprite).color = new Color(0,255,0,255);
      } else  {
        console.log(dragable.getComponent(Sprite).color)
        console.log(new Color(255,0,0,255))
        dragable.getComponent(Sprite).color = new Color(255,0,0,255);}
    });
  }
  _feedback(feedtype, dragable) {
    console.log("in feedback",feedtype);
   // const colorarray = [new Color(0,255,0,255),new Color(255,0,0,255)];
    dragable.node.getComponent(Sprite).color = new Color(0,255,0,255);
  }
}
